*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: sans-serif;
}

.Container {
  max-width: 550px;
  margin: 2rem auto;
  border: 2px solid steelblue;
  padding: 2rem;
  /* background: rgba(70, 30, 180, 0.2); */
}

.header {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
}

.header h1 {
  font-size: 1.8rem;
  font-weight: 500;
  letter-spacing: 1px;
}

.btn {
  display: inline-block;
  background: #000;
  color: #fff;
  border: none;
  padding: 10px 20px;
  margin: 5px;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  font-size: 15px;
  font-family: inherit;
  text-transform: uppercase;
}

/* .tasks {
  padding: 2rem 0;
} */

.task {
  width: 100%;
  background: rgba(70, 30, 180, 0.5);
  margin: 1rem 0;
  padding: 0.5rem 1rem;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.task h3 {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.task.reminder {
  border-left: 4px solid green;
}

.add-Task {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin: 2rem 0;
}

.add-Task .form-control {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.add-Task .form-control input {
  padding: 0.5rem;
  outline: none;
}

.form-control-check {
  flex-direction: row !important;
}

.form-control-check input {
  margin-left: 4rem !important;
}
